<template>
  <div>
    <el-card>
      <h3>相册</h3>
      <div class="operation">
        <el-button @click="dialogVisible = true">
          创建相册
        </el-button>
      </div>
      <ul class="albumlist">
        <router-link
          :to="'/album/' + item.albumid"
          v-for="item in albumlist"
          :key="item.albumid"
        >
          <li>
            <div class="albumlist-img">
              <img
                src="../assets/img/1.jpg"
                width="164px"
                height="164px"
              >
            </div>

            <div class="albumlist-b">
              <span>{{ item.albumname || '相册名称' }}</span>
              <el-button
                type="danger"
                size="mini"
                icon="el-icon-delete"
                @click.prevent="del(item)"
              />
            </div>
          </li>
        </router-link>
      </ul>
    </el-card>
    <el-dialog
      center
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      title="创建相册"
      width="50%"
    >
      <el-form
        :rules="rules"
        ref="addalbumFormref"
        :model="addalbumForm"
      >
        <el-form-item
          prop="albumname"
          label="相册名称"
        >
          <el-input v-model="addalbumForm.albumname" />
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="submit('addalbumFormref')">提交</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      userid: JSON.parse(window.sessionStorage.user).id,
      dialogVisible: false,
      dfUrl: 'http://localhost:10086/ps/',
      albumlist: [{}],
      addalbumForm: {},
      rules: {
        albumname: [{ required: true, message: '请输入相册名', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getalbumlist()
  },
  methods: {
    // 获取用户相册
    async getalbumlist () {
      const { data: res } = await this.$http.get('album/list', { params: { userid: this.userid } })
      this.albumlist = res.obj
    },
    // 提交添加相册信息
    submit (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        this.addalbumForm.albumuserid = this.userid
        const { data: res } = await this.$http.post('user/addAlbum', this.addalbumForm)
        this.handleClose()
        this.getalbumlist()
      })
    },
    async del (item) {
      // 弹窗询问是否删除
      const confirmres = await this.$confirm('是否确认删除该相册?', '确认删除', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { albumid:id } = item
      const { data: res } = await this.$http.delete('user/delAlbum', { params: { id } })
      this.getalbumlist()
    },
    // 关闭对话框
    handleClose () {
      this.dialogVisible = false
    }
  }
}
</script>
<style lang="less" scoped>
.el-card {
  background-color: #e8f0d8;
}
.albumlist {
  display: flex;
  list-style: none;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  margin-top: 20px;
  li {
    width: 174px;
    height: 220px;
    padding: 5px;
    margin-bottom: 10px;
    background-color: #fff;
    box-shadow: 1px 1px 10px #3332;
  }
  a:not(:nth-child(7n)) > li {
    margin-right: 11px;
  }
  .albumlist-b {
    display: flex;
    height: 46px;
    align-items: center;
    justify-content: space-between;
  }
  a:hover {
    text-decoration: none;
  }
}
</style>
