<template>
  <div class="login_container">
    <div />
    <div class="bg" />
    <div class="login_box">
      <h1>登录</h1>
      <!-- 登录表单 -->
      <el-form
        lable-width="0px"
        class="login-form"
        ref="loginFormref"
        :model="loginForm"
        :rules="rules"
      >
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            ref="username"
            placeholder="手机号"
          >
            <i
              slot="prefix"
              class="el-input__icon el-icon-phone"
            />
          </el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="loginForm.password"
            ref="password"
            placeholder="密码"
          >
            <i
              slot="prefix"
              class="el-input__icon el-icon-lock"
            />
          </el-input>
        </el-form-item>
        <!-- 按钮 -->
        <el-form-item>
          <el-button
            type="primary"
            @click="submitForm('loginFormref')"
          >
            登录
          </el-button>
        </el-form-item>
        <el-form-item>
          <router-link to="/register">
            还没有账号？立即注册
          </router-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      loginForm: {},
      rules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  mounted () {
    // 监听回车事件
    document.onkeydown = e => {
      const _key = window.event.keyCode
      if (_key === 13) {
        this.submitForm('loginFormref')
      }
    }
    // 监听输入框内容，如果为空，则获取焦点
    if (this.loginForm.username === '') {
      this.$refs.username.focus()
    } else if (this.loginForm.password === '') {
      this.$refs.password.focus()
    }
  },
  methods: {
    // 提交表单
    submitForm (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        const { data: res } = await this.$http.post('login', this.loginForm)
        console.log(res);
        if (res.code === 200) {
          this.$message({
            type: 'success',
            message: '登录成功！'
          })
          const user = { id: res.obj.userid || res.obj.adminid,username:res.obj.username ||res.obj.adminname, status: res.obj.status }
          window.sessionStorage.setItem('user', JSON.stringify(user))
          this.$router.push('/')
        } else {
          this.$message({
            type: 'error',
            message: '用户名或密码错误！'
          })
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
a:hover {
  text-decoration: none;
}
.login_container {
  height: 100%;
  background: url('http://47.115.58.32:8080/img/login-bg.jpg') 100%;
  background-color: rgb(46, 196, 183);
}
.bg {
  width: 390px;
  height: 480px;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 50%;
  left: 80%;
  transform: translate(-50%, -50%);
}
.login-form {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  .el-button {
    width: 100%;
  }
  .el-form-item:last-child {
    text-align: right;
  }
}
.login_box {
  width: 350px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 80%;
  transform: translate(-50%, -50%);
  border-radius: 4px;
  h1 {
    width: 100%;
    margin: 20px 0;
    text-align: center;
    color: rgb(23, 160, 240);
  }
}
</style>
